.sync-state-result-container{height:100%;display:flex;flex-direction:column;justify-content:space-between}.sync-state-result-container .update-result-container{display:flex;flex-direction:column}.sync-state-result-container .update-result-container .update-result-title{padding-left:5px}.sync-state-result-container .update-result-container .update-result-list{display:flex;flex-direction:column;border:1px solid #cfd2d7;border-radius:10px;padding:10px;box-shadow:inset 0 0 6px 0 #a9a9a9;background-color:#f0f0f0}.sync-state-result-container .update-result-line{display:flex;flex-direction:row;justify-content:space-between}.sync-state-result-container button{height:30px;border-radius:5px;cursor:pointer;background-color:#eaecf1;border:1px solid #eaecf1;transition:border-color .2s ease}.sync-state-result-container button:hover{border-color:#1e47a9}