.progress-bar{border:1px solid #cfd2d7;height:30px;border-radius:10px;box-shadow:inset 0 0 6px 0 #a9a9a9;background:#fff;position:relative;overflow:hidden}.progress-bar .progress-bar-line{display:flex;flex-direction:row;justify-content:center;align-items:center;width:100%;height:100%;font-weight:700}.progress-bar .progress-bar-back{height:100%;background:#a3a3fa;width:100%;position:absolute;top:0;border-radius:5px;color:#fff}.progress-bar .progress-bar-front{position:absolute;top:0;background:#fff;color:#000;transition:clip-path 2s ease}